<template>
  <div>
    <v-container>
      <v-row>
        <v-col cols="12">
          <!-- data table -->
          <crud-data-table
            :table="{
              heading: $t('crud.table.headings.utilities.images'),
              showSelect: true,
              search: true,
              button: {
                text: $t('crud.table.addNewButtons.utilities.images')
              }
            }"
            :add-edit-dialog="{
              addHeading: $t('addEditDialog.headings.add.utilities.images'),
              editHeading: $t('addEditDialog.headings.edit.utilities.images')
            }"
            :headers="headers"
            :default-item="item"
            :actions="{
              edit: true,
              delete: true
            }"
            :fetch-data="{
              url: '/api/images',
              data: 'images'
            }"
            :add-data="{
              url: '/api/images',
              formData: true,
              reload: true
            }"
            :edit-data="{
              url: '/api/images/:param',
              param: '_id',
              formData: true,
              reload: true
            }"
            :delete-data="{
              url: '/api/images/:param',
              param: '_id',
              reload: true
            }"
            :delete-multiple-data="{
              url: '/api/images/delete',
              arrayOf: '_id',
              reload: true
            }"
            @after-add="$store.dispatch('image/fetchImages', { axios: $axios })"
            @after-edit="
              $store.dispatch('image/fetchImages', { axios: $axios })
            "
            @after-delete="
              $store.dispatch('image/fetchImages', { axios: $axios })
            "
          >
            <template v-slot:[`item.image`]="{ item: image }">
              <v-img
                width="100px"
                :src="'/uploads/images/' + image.image"
              ></v-img>
            </template>
            <template v-slot:crud-form="{ item: image }">
              <image-form :image="image" />
            </template>
          </crud-data-table>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
export default {
  components: {
    CrudDataTable: () => import('@/components/crud/CrudDataTable'),
    ImageForm: () => import('@/components/forms/utilities/ImageForm')
  },
  middleware: ['pagePermission'],
  data() {
    return {
      headers: [
        { text: this.$t('crud.table.headers.title'), value: 'title' },
        { text: this.$t('crud.table.headers.slug'), value: 'slug' },
        { text: this.$t('crud.table.headers.image'), value: 'image' },
        {
          text: this.$t('crud.table.headers.actions'),
          value: 'actions',
          sortable: false
        }
      ],
      item: { title: '', slug: '', image: null }
    }
  }
}
</script>

